import "./blogPosts.scss";

const BlogPostCard = ({ post = {} }) => {
  console.log(post)
  const data = post.data;
  const primaryImage = data.body.find(item => item.type === "image");
  const imageUrl = primaryImage
    ? primaryImage.url
    : "/static/assets/images/no-image.png";
  return (
    <div className="blog__posts__item">
      <div className="blog__posts__image-box">
        <div
          className="blog__posts__image"
          style={{
            backgroundImage: `url(${imageUrl})`
          }}
        />
      </div>
      <div className="blog__posts__meta">
        <span className="date">jun 10, 2018</span>
      </div>
      <div className="blog__posts__content">
        <h3 className="blog__posts__title">
          Contrary to popular belief, Lorem Ipsum{" "}
        </h3>
        <p className="blog__posts__desc">
          Lorem Ipsum is simply dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy text ever
          since the 1500s
        </p>
      </div>
      <div className="read__more" />
    </div>
  );
};

export default BlogPostCard;
